exports.push([module.id, "\n#timecontent {\r\n  font-weight: bold;\r\n  text-align: center;\n}\n#time #hour {\r\n  font-size: 6.7em;\n}\n#time #second {\r\n  font-size: 3em;\n}\n#day > span {\r\n  display: inline-block;\r\n  width: 50%;\r\n  font-size: 1.8em;\n}\r\n", "", {"version":3,"sources":["D:/workspace/home-tools/timeshow.vue"],"names":[],"mappings":";AA+CA;EACA,kBAAA;EACA,mBAAA;CACA;AACA;EACA,iBAAA;CACA;AACA;EACA,eAAA;CACA;AACA;EACA,sBAAA;EACA,WAAA;EACA,iBAAA;CACA","file":"timeshow.vue","sourcesContent":["<template>\r\n<div id=\"timecontent\">\r\n  <div>\r\n    <div id=\"time\"><span id=\"hour\">{{hour}}</span><span id=\"second\">:{{second}}</span></div>\r\n    <div id=\"day\"><span>{{day}}</span><span>{{week}}</span></div>\r\n  </div>\r\n</div>\r\n</template>\r\n\r\n<script type=\"text/javascript\">\r\nimport moment from \"moment\";\r\nexport default {\r\n  data() {\r\n    var d = moment();\r\n    return {\r\n      day: this.formatDate(d),\r\n      hour: this.formatTime(d),\r\n      second: d.second(),\r\n      week: d.weekday(),\r\n    };\r\n  },\r\n  created: function() {\r\n    var that = this;\r\n    setInterval(function() {\r\n      var d = moment();\r\n      that.day = that.formatDate(d);\r\n      that.time = that.formatTime(d);\r\n      that.second = d.second();\r\n      //晚上10点后背景黑色\r\n      if (d.hour() >= 22 || d.hour() <= 6) {\r\n        that.$parent.backgroundClass = \"night\";\r\n      } else {\r\n        that.$parent.backgroundClass = \"b1\";\r\n      }\r\n    }, 1000);\r\n  },\r\n  methods: {\r\n    formatTime(m) {\r\n      return m.format(\"HH:mm\");\r\n    },\r\n    formatDate(m) {\r\n      return m.format(\"YYYY/MM/DD\");\r\n    }\r\n  }\r\n};\r\n</script>\r\n<style type=\"text/css\">\r\n#timecontent {\r\n  font-weight: bold;\r\n  text-align: center;\r\n}\r\n#time #hour {\r\n  font-size: 6.7em;\r\n}\r\n#time #second {\r\n  font-size: 3em;\r\n}\r\n#day > span {\r\n  display: inline-block;\r\n  width: 50%;\r\n  font-size: 1.8em;\r\n}\r\n</style>"],"sourceRoot":""}]);

